/** 常用色值 **/
/** 常用字体大小 **/
.header {
  background-color: #ffffff;
}
.header .header-top {
  height: 60px;
  line-height: 60px;
  padding: 0 20px;
  text-align: right;
}
.header .header-top .logo {
  line-height: 60px;
  text-align: left;
  font-size: 18px;
}
.header .header-top .logo img {
  height: 40px;
  vertical-align: middle;
}
.header .header-top .logo span {
  margin-left: 5px;
}
.header .header-top a {
  margin-left: 40px;
}
.header .breadcrumb {
  height: 40px;
  line-height: 40px;
  padding: 0 20px;
  border-top: 1px solid #f9c700;
}
.header .breadcrumb .breadcrumb-title {
  text-align: center;
  font-size: 18px;
}
.header .breadcrumb .breadcrumb-title:after {
  position: absolute;
  content: '';
  left: 73px;
  top: 39px;
  border-top: 9px solid #ffffff;
  border-left: 12px solid transparent;
  border-right: 12px solid transparent;
}
.header .breadcrumb .weather {
  text-align: right;
  font-size: 14px;
}
.header .breadcrumb .weather .date {
  margin-right: 10px;
  vertical-align: middle;
}
.header .breadcrumb .weather .weather-img img {
  height: 15px;
}
.header .breadcrumb .weather .weather-detail {
  margin-left: 5px;
  vertical-align: middle;
}
.simple-page .header-top {
  background: #1890ff;
  color: #ffffff;
}

/** 常用色值 **/
/** 常用字体大小 **/
.footer {
  height: 100px;
  padding: 40px 0;
  text-align: center;
  color: #d7d7d7;
}

.logo {
  line-height: 90px;
  padding-left: 20px;
}
.logo img {
  height: 35px;
}
.logo h1 {
  color: white;
  font-size: 20px;
  display: inline-block;
  vertical-align: middle;
  margin: 0 0 0 10px;
}

/** 常用色值 **/
/** 常用字体大小 **/
/** load **/
.ajax-loading {
  display: none;
}
.ajax-loading .loading {
  position: fixed;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  padding: 0 40px;
  height: 80px;
  line-height: 80px;
  background: rgba(0, 0, 0, 0.75);
  border-radius: 6px;
  text-align: center;
  z-index: 9999;
  font-size: 16px;
  color: #fff;
}
.ajax-loading .loading img {
  width: 32px;
  vertical-align: middle;
}
.ajax-loading .loading span {
  margin-left: 12px;
}
.ajax-loading .overlay {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 9998;
  background: #ffffff;
  opacity: 0.1;
}
/****/
ul,
li {
  list-style: none;
}
.clearfix::after {
  content: ' ';
  clear: both;
  display: block;
  visibility: hidden;
}
.container .nav-left {
  height: calc(100vh);
  background-color: #001529;
}
.container .main {
  height: calc(100vh);
  background-color: #f1f3f5;
  overflow: auto;
}
.container .content {
  height: calc(100vh - 101px);
  position: relative;
  padding: 20px;
  overflow: auto;
}
.content-wrap {
  background: #ffffff;
  border: 1px solid #e8e8e8;
  margin-top: -3px;
}
.content-wrap .ant-table-wrapper {
  margin-left: -1px;
  margin-right: -2px;
}
.ant-card button {
  margin-right: 10px;
}

/** 常用色值 **/
/** 常用字体大小 **/
.home-warp {
  background-color: #ffffff;
  height: calc(70vh);
  display: flex;
  align-items: center;
  justify-content: center;
}

.card-wrap {
  margin-bottom: 10px;
}
.card-wrap button {
  margin-right: 10px;
}
/* modals */
/* use css to set position of modal */
.vertical-center-modal {
  text-align: center;
  white-space: nowrap;
}
.vertical-center-modal:before {
  content: '';
  display: inline-block;
  height: 100%;
  vertical-align: middle;
  width: 0;
}
.vertical-center-modal .ant-modal {
  display: inline-block;
  vertical-align: middle;
  top: 0;
  text-align: left;
}
/* For demo */
.ant-carousel .slick-slide {
  text-align: center;
  height: 160px;
  line-height: 160px;
  background: #364d79;
  overflow: hidden;
}
.ant-carousel .slick-slide h3 {
  color: #fff;
}
.slider-wrap .ant-carousel .slick-slide {
  height: 240px !important;
}

